<template>
  <v-container fluid id="pool">
    <v-row style="margin-block: 20px 50px" no-gutters justify="center">
      <v-col cols="12" sm="10" lg="7">
        <v-col align="center" class="contNavBtn">
          <a v-for="(item, index) in dataNavButtons" :key="index"
            :href="item.to" @click="activeClass(item)">
            <v-btn class="navBtn h7-em" :class="{active: item.active}">
              {{ item.title }}
            </v-btn>
          </a>
        </v-col>
        <router-view></router-view>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "Pool",
  i18n: require("./i18n"),
  data () {
    return {
      dataNavButtons: [
        {
          title: "Standard",
          to: "#/pool/standard",
          active: false,
        },
        {
          title: "Stable",
          to: "#/pool/stable",
          active: true,
        },
      ]
    }
  },
  methods: {
    activeClass(item) {
      if (item.title == "Standard") {
        this.dataNavButtons[1].active = false
        item.active = true
      }
      if (item.title == "Stable") {
        this.dataNavButtons[0].active = false
        item.active = true
      }
    },
  }
};
</script>
