        // do something e.g hide loader
      }
    },

    // swap qoute
    async Quote() {
      // enable web3 instance using moralise sdk
      window.web3 = await Moralis.enable();
      // contract connection instance
      let contract_instance = new web3.eth.Contract(
        this.contract_abi,
        this.contract_address
      );

      // transaction instance by calling contract function
      let result = await contract_instance.methods
        // all function values in arrays []
        .quote(this.amountA, this.reserveA, this.reserveB)
        .send({ from: this.userwalletaddress });
      // get access to event if any
      console.log(result.events);
      if (result["status"] == true) {
        // do something e.g hide loader
      }
    },
  },
};
</script>

