<script>
// import store from '../../store/index'
import Alert from "@/pages/Alert/Alert.vue";
import MenuBridgeIcon from "@/pages/Bridge/MenuBridgeIcon.vue";

export default {
  name: "BridgeIcon",
  i18n: require("./i18n"),
  components: {
      Alert,
      MenuBridgeIcon
  },
  mounted() {
    if (this.nombreFrom == "Evmos") {
      this.filterFrom = true
    } else {
      this.filterFrom = false
    }
    if (this.nombreTo == "Evmos") {
      this.filterTo = true
    } else {
      this.filterTo = false
    }
  },
  data() {
    return {
      input: "",
      // lengthInput: '#########',
      nombreData: this.$t('title'),
      logoData: require("@/assets/logo/database.png"),
      filterData: false,
      nombreFrom: 'Ethereum',
      logoFrom: require("@/assets/logo/ethereum.png"),
      filterFrom: false,
      nombreTo: 'Evmos',
      logoTo: require("@/assets/logo/logo.png"),
      filterTo: false,
    }
  },
  methods: {
    limitNumber (id) {
      var input = document.getElementById(id);
      input.addEventListener('input', function() {
        if (this.value.length > 8) {
          this.value = this.value.slice(0, 8)
        }
        if (this.value.length > 6 && window.innerWidth <= 380) {
          this.value = this.value.slice(0, 6)
        }
      })
    },
    SelectToken (item) {
      this.nombreData = item.name;
      this.logoData = item.logo;
      if (this.nombreData == "Evmos") {
        this.filterData = true
      } else {
        this.filterData = false
      }
      this.$refs.menu.menu = false;
    },
    SelectTokenFrom (item) {
      if (item.name == this.nombreTo) {
        let logo = this.logoFrom;
        let nombre = this.nombreFrom;

        this.logoFrom = item.logo
        this.nombreFrom = item.name
        if (this.nombreFrom == "Evmos") {
          this.filterFrom = true
        } else {
          this.filterFrom = false
        }

        this.logoTo = logo
        this.nombreTo = nombre
        if (this.nombreTo == "Evmos") {
          this.filterTo = true
        } else {
          this.filterTo = false
        }
        this.$refs.menu.menuFrom = false;
      } else {
        this.nombreFrom = item.name;
        this.logoFrom = item.logo;
        if (this.nombreFrom == "Evmos") {
          this.filterFrom = true
        } else {
          this.filterFrom = false
        }
        this.$refs.menu.menuFrom = false;
      }
    },
    SelectTokenTo (item) {
      if (item.name == this.nombreFrom) {
        let logo = this.logoTo;
        let nombre = this.nombreTo;

        this.logoTo = item.logo
        this.nombreTo = item.name
        if (this.nombreTo == "Evmos") {
          this.filterTo = true
        } else {
          this.filterTo = false
        }

        this.logoFrom = logo
        this.nombreFrom = nombre
        if (this.nombreFrom == "Evmos") {
          this.filterFrom = true
        } else {
          this.filterFrom = false
        }
        this.$refs.menu.menuTo = false;
      } else {
        this.nombreTo = item.name;
        this.logoTo = item.logo;
        if (this.nombreTo == "Evmos") {
          this.filterTo = true
        } else {
          this.filterTo = false
        }
        this.$refs.menu.menuTo = false;
      }
    },
    AlertaSuccess() {
      this.$refs.alerta.AlertSuccess();
    },
  }
};
