<template>
  <section>
    <Alert ref="alerta"></Alert>
    <v-card class="containerPanel2">
      <v-row style="margin: 12px" justify="center">
        <v-col xs="12" sm="12" cols="12" md="12" lg="12" xl="11">
          <span class="swap clr h4-em">{{$t('text')}}</span>
          <p class="textSwap clr h6-em">{{$t('text1')}}</p>
        </v-col>
        <v-col xs="12" sm="12" cols="12" md="12" lg="12" xl="11" class="spaceDeleteMobile">
          <p class="balance h6-em clr">
            {{$t('text2')}}
            <span style="color: #888787">0</span>
            {{ tokenNameUp }}
          </p>
          <v-card class="containerTextField">
            <div class="containerExs openMenuAAddRemove">
              <img class="logo" :src="logoUp" alt="token"
                :class="{filter: filterUp}" />
              <p class="exs clr h6-em" style="cursor: pointer"> {{ nombreUp }} </p>
              <v-icon
                medium
                class="flechita2"
              >
                mdi-chevron-down
              </v-icon>
            </div>

            <v-text-field
              v-model="inputA"
              id="inputA"
              hide-spin-buttons
              type="number"
              class="textField"
              solo
              placeholder="0.00"
              @keypress="limitNumber('inputA')"
            ></v-text-field>
            <v-btn class="botonMax text-xl-h6"> Max </v-btn>
          </v-card>
        </v-col>

        <v-col xs="3" cols="3" sm="3" md="2" lg="2" xl="4" class="padd2 paddRecover">
          <aside class="centrarFlechaAbajo">
            <v-btn icon class="containerPlus" style="cursor: default">
                <v-icon
                medium
                class="plus"
                >
                mdi-plus
                </v-icon>
            </v-btn>
          </aside>
        </v-col>

        <v-col xs="12" sm="12" cols="12" md="12" lg="12" xl="11"
          class="spaceDeleteMobile" style="padding-top: 0">
          <p class="balance h6-em clr">
            Max:
            <span style="color: #888787">0</span>
            {{ tokenNameDown }}
          </p>
          <v-card class="containerTextField">
            <div class="containerExs openMenuBAddRemove">
              <img class="logo" :src="logoDown" alt="token"
                :class="{filter: filterDown}" />
              <p class="exs h6-em clr">{{ nombreDown }}</p>
              <v-icon
                medium
                class="flechita2"
              >
                mdi-chevron-down
              </v-icon>
            </div>
              
            <v-text-field
              v-model="inputB"
              id="inputB"
              hide-spin-buttons
              type="number"
              class="textField"
              solo
              placeholder="0.00"
              @keypress="limitNumber('inputB')"
            ></v-text-field>
            <v-btn class="botonMax text-xl-h6"> Max </v-btn>
          </v-card>
        </v-col>

        <v-col xs="12" sm="12" cols="12" md="12" lg="12" xl="11"
          class="paddRecover" style="padding-bottom: 0">
          <div class="center">
            <p class="depositText h6-em clr">{{$t('text3')}}</p>
            <p class="depositText h6-em clr childCenter">0.1%</p>
          </div>
        </v-col>

        <v-col xs="12" sm="12" cols="12" md="12" lg="12" xl="11">
          <div class="center">
            <p class="depositText h6-em clr">{{$t('text4')}}</p>
            <p class="depositText h6-em clr childCenter">0.1%</p>
          </div>
        </v-col>

        <v-col xs="11" sm="11" cols="11" md="11" lg="11" xl="11"
          style="padding: 0" class="paddRecover">
          <div style="display: flex; justify-content: space-between">
            <v-btn
              id="botonAprove"
              class="h6-em aproveSupply"
              v-bind:class="{ aproveSupplyActive: aproveSupplyA }"
              @click="AproveSupplyA"
            >
              {{$t('bot1')}}
            </v-btn>
            <v-btn
              id="botonAprove"
              class="h6-em aproveSupply"
              v-bind:class="{ aproveSupplyActive: aproveSupplyB }"
              @click="AproveSupplyB"
            >
              {{$t('bot2')}}
            </v-btn>
          </div>
        </v-col>

        <MenuAddRemove ref="menu"></MenuAddRemove>

      </v-row>
    </v-card>
  </section>
</template>

<script>
import Alert from "@/pages/Alert/Alert.vue";
import MenuAddRemove from "./MenuAddRemove.vue";

export default {
  name: "Add",
  i18n: require("./i18n"),
  components: {
    Alert,
    MenuAddRemove
  },
  mounted() {
    if (this.nombreUp == "EXS") {
      this.filterUp = true
    } else {
      this.filterUp = false
    }
    if (this.nombreDown == "EXS") {
      this.filterDown = true
    } else {
      this.filterDown = false
    }
  },
  data() {
    return {
      inputA: "",
      inputB: "",
      tokenNameUp: this.$t('a3'),
      nombreUp: "EXS",
      logoUp: require("@/assets/logo/logo.png"),
      filterUp: false,
      tokenNameDown: "OSMO",
      nombreDown: "OSMO",
      logoDown: require("@/assets/logo/OSMO.png"),
      filterDown: false,
      aproveSupplyA: false,
      aproveSupplyB: false,
    };
  },
  methods: {
    limitNumber (id) {
      var input = document.getElementById(id);
      input.addEventListener('input', function() {
        if (this.value.length > 12) {
          this.value = this.value.slice(0, 12)
        }
      })
    },
    SelectTokenA (item) {
      if (item.name == this.nombreDown) {
        let logo = this.logoUp;
        let nombre = this.nombreUp;

        this.tokenNameUp = item.name;
        this.tokenNameDown = nombre;
        this.logoUp = item.logo
        this.nombreUp = item.name
        if (this.nombreUp == "EXS") {
          this.filterUp = true
        } else {
          this.filterUp = false
        }

        this.logoDown = logo
        this.nombreDown = nombre
        if (this.nombreDown == "EXS") {
          this.filterDown = true
        } else {
          this.filterDown = false
        }
        this.$refs.menu.menuA = false;
      } else {
        this.tokenNameUp = item.name;
        this.nombreUp = item.name;
        this.logoUp = item.logo;
        if (this.nombreUp == "EXS") {
          this.filterUp = true
        } else {
          this.filterUp = false
        }
        this.$refs.menu.menuA = false;
      }
      var existing = JSON.parse(localStorage.getItem('reciente'))
      existing = existing || []
      var index
      for (var i = 0; i < existing.length; i++) {
        if (existing[i].name === item.name) {
          index = i
          break
        }
      }
      if (index === undefined) existing.push(item) 
      localStorage.setItem('reciente', JSON.stringify(existing));
      this.$refs.menu.recentBy = JSON.parse(localStorage.getItem('reciente'))
    },
    SelectTokenB (item) {
      if (item.name == this.nombreUp) {
        let logo = this.logoDown;
        let nombre = this.nombreDown;

        this.tokenNameDown = item.name;
        this.tokenNameUp = nombre;
        this.logoDown = item.logo
        this.nombreDown = item.name
        if (this.nombreDown == "EXS") {
          this.filterDown = true
        } else {
          this.filterDown = false
        }

        this.logoUp = logo
        this.nombreUp = nombre
        if (this.nombreUp == "EXS") {
          this.filterUp = true
        } else {
          this.filterUp = false
        }
        this.$refs.menu.menuB = false;
      } else {
        this.tokenNameDown = item.name;
        this.nombreDown = item.name;
        this.logoDown = item.logo;
        if (this.nombreDown == "EXS") {
          this.filterDown = true
        } else {
          this.filterDown = false
        }
        this.$refs.menu.menuB = false;
      }
      var existing = JSON.parse(localStorage.getItem('reciente'))
      existing = existing || []
      var index
      for (var i = 0; i < existing.length; i++) {
        if (existing[i].name === item.name) {
          index = i
          break
        }
      }
      if (index === undefined) existing.push(item) 
      localStorage.setItem('reciente', JSON.stringify(existing));
      this.$refs.menu.recentBy = JSON.parse(localStorage.getItem('reciente'))
    },
    AproveSupplyA() {
      if (this.aproveSupplyA == false) {
        this.aproveSupplyA = true;
        this.aproveSupplyB = false;
        this.$refs.alerta.AlertSuccess();
      }
    },
    AproveSupplyB() {
      if (this.aproveSupplyB == false) {
        this.aproveSupplyB = true;
        this.aproveSupplyA = false;
        this.$refs.alerta.AlertSuccess();
      }
    },
  }
};
</script>

<style scoped>
.openMenuAAddRemove {
  cursor: pointer;
}
.openMenuBAddRemove {
  cursor: pointer;
}
</style>
