import Vue from 'vue';
import Router from 'vue-router';

import Layout from '@/components/Layout/Layout';
import Landing from '@/components/Landing/Landing';

// Pages
import Swap from '@/pages/Swap/Swap';
import Pool from '@/pages/Pool/Pool';
import AddRemove from '@/pages/Pool/AddRemove/AddRemove';
import StablePools from '@/pages/Pool/StablePools';
import BridgeSimple from '@/pages/Bridge/BridgeSimple';
import BridgeColor from '@/pages/Bridge/BridgeColor';
import BridgeIcon from '@/pages/Bridge/BridgeIcon';
import BridgeMulti from '@/pages/Bridge/BridgeMulti';
import Assets from '@/pages/Assets/Assets';
import AvailableAssets from '@/pages/Assets/AvailableAssets';
import Farms from '@/pages/Assets/Farms';
import Error from "@/pages/Error/Error";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Landing',
      component: Landing,
    },
    {
      path: '/swap',
      name: 'Layout',
      component: Layout,
      children: [
        {
          path: '/swap',
          name: 'Swap',
          component: Swap,
        },
        {
          path: '/pool',
          name: 'Pool',
          component: Pool,
          children: [
            {
              path: 'standard',
              name: 'Standard',
              component: AddRemove,
            },
            {
              path: 'stable',
              name: 'StablePools',
              component: StablePools,
            },
          ]
        },
        {
          path: '/celer-bridge',
          name: 'BridgeSimple',
          component: BridgeSimple,
        },
        {
          path: '/synapse-bridge',
          name: 'BridgeColor',
          component: BridgeColor,
        },
        {
          path: '/nomad-bridge',
          name: 'BridgeIcon',
          component: BridgeIcon,
        },
        {
          path: '/multichain-bridge',
          name: 'BridgeMulti',
          component: BridgeMulti,
        },
        {
          path: '/assets',
          name: 'Assets',
          component: Assets,
        },
        {
          path: '/available-assets',
          name: 'Available-Assets',
          component: AvailableAssets,
        },
        {
          path: '/farms',
          name: 'Farms',
          component: Farms,
        }
      ],
    },
    {
      path: '*',
      name: 'Error',
      component: Error
    }
  ],
});
