<template>
  <v-container fluid id="margenFooter">
    <Alert ref="alerta"></Alert>
    <v-row justify="center" style="margin-top: 40px" id="margenBridge">
      <v-col xs="12" sm="7" cols="12" md="5" lg="4" xl="5" align="center">
        <v-card class="containerBridge">
          <v-row justify="center">
            <v-col  xs="10" sm="12" cols="10" md="11" lg="11" xl="11" style="margin-top: 10px" align="start">
              <span id="bridge" class="text-h4">{{$t('title1')}}</span>
            </v-col>

            <v-col xs="11" sm="12" cols="11" md="12" lg="12" xl="12">
              <v-card id="colorContainerCentrarBridge">
                <v-col style="padding-bottom: 0; padding-top: 0">
                  <p id="balance2" class="text-h6">
                    Max:
                    <span style="color: #888787">0</span>
                    {{ tokenName }}
                  </p>
                  <div style="display: flex; margin-bottom: 0.89777875rem">
                    <p id="fromTo" class="text-h6">{{$t('t1')}}</p>
                    <v-chip
                      filter
                      id="colorChipBridgeSimple"
                      style="display: flex; align-items: center; border-radius: 9.56446px"
                      class="openMenuFromB"
                    >
                    <img class="imgChip" :src="logoFrom"
                      :class="{filter: filterFrom}">
                     <span style="font-size: 1.3rem !important;">{{ nombreFrom }}</span>
                      <v-icon style="margin-left: 0.5rem" small>mdi-chevron-down</v-icon>
                    </v-chip>
                  </div>

                  <v-card class="containerTextField" id="reduccionFuenteBridge">
                    <v-text-field
                      v-model="inputA"
                      id="inputA"
                      hide-spin-buttons
                      type="number"
                      class="textField"
                      solo
                      placeholder="0.00"
                      @keypress="limitNumber('inputA')"
                    ></v-text-field>
                      <section>
                        <aside style="display: flex; justify-content: end">
                          <a
                            id="exs"
                            class="text-h6"
                            href="#"
                            style="text-decoration: underline !important; margin: 0px 0.67233125rem;
                              margin-bottom: 0.125rem !important"
                          >
                            Max
                          </a>
                        </aside>
                      <div class="containerExs openMenuABridge" style="padding-block: 0.5rem;">
                        <img class="logo" :src="logoUp" alt="token"
                          :class="{filter: filterUp}" />
                        <p id="exs" class="text-h6" style="cursor: pointer"> {{ nombreUp }} </p>
                        <v-icon
                          medium
                          class="flechita2"
                        >
                          mdi-chevron-down
                        </v-icon>
                      </div>
                    </section>
                  </v-card>
                </v-col>
              </v-card>
            </v-col>

            <v-col xs="3" cols="3" sm="3" md="4" lg="4" xl="4" class="padd2 paddRecover">
              <aside class="centrarFlechaAbajo">
                <v-btn
                  icon
                  class="containerFlechaAbajo"
                  style="padding: 0 !important"
                  @click="SwitchButton"
                >
                  <v-icon
                    medium
                    class="flechita3"
                    :class="{rotate: rotateArrow}"
                  >
                    mdi-chevron-down
                  </v-icon>
                </v-btn>
              </aside>
            </v-col>

            <v-col xs="11" sm="12" cols="11" md="12" lg="12" xl="12">
              <!-- <v-card style="background: #A093C5"> -->
                <v-card style="background: transparent">
                <v-col>
                  <div style="display: flex; margin-bottom: 0.89777875rem">
                    <p id="fromTo" class="text-h6">{{$t('t2')}}</p>
                    <v-chip
                      filter
                      id="colorChipBridgeSimple"
                      style="display: flex; align-items: center; border-radius: 9.56446px"
                      class="openMenuToB"
                    >
                    <img class="imgChip" :src="logoTo"
                      :class="{filter: filterTo}">
                      <span style="font-size: 1.3rem !important;">{{ nombreTo }}</span>
                      <v-icon style="margin-left: 0.5rem" small>mdi-chevron-down</v-icon>
                    </v-chip>
                  </div>
                    <v-card class="containerTextField" id="reduccionFuenteBridge">
                      <v-text-field
                        v-model="inputB"
                        id="inputB"
                        hide-spin-buttons
                        type="number"
                        class="textField"
                        solo
                        placeholder="0.00"
                        @keypress="limitNumber('inputB')"
                      ></v-text-field>
                    </v-card>
                </v-col>
              </v-card>
            </v-col>

            <v-col xs="11" sm="12" cols="11" md="12" lg="12" xl="12">
              <v-btn id="botonBridge" class="text-h4" @click="AlertaSuccess">{{$t('but')}}</v-btn>
            </v-col>
          </v-row>
        </v-card>

        <v-row justify="center" align="center" style="margin-top: 3.71049375rem !important">
          <v-col cols="12">
            <div style="display: flex; justify-content: center">
              <span class="footerBridge text-h5">
                {{$t('t4')}}
              </span>
            </div>
          </v-col>
        </v-row>

        <MenuBridgeColor ref="menu"></MenuBridgeColor>

      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Alert from "@/pages/Alert/Alert.vue";
import MenuBridgeColor from "@/pages/Bridge/MenuBridgeColor.vue";

export default {
  name: "BridgeSimple",
  i18n: require("./i18n"),
  components: {
      Alert,
      MenuBridgeColor
  },
  mounted() {
    if (this.nombreFrom == "Evmos") {
      this.filterFrom = true
    } else {
      this.filterFrom = false
    }
    if (this.nombreUp == "EXS") {
      this.filterUp = true
    } else {
      this.filterUp = false
    }
    if (this.nombreTo == "Evmos") {
      this.filterTo = true
    } else {
      this.filterTo = false
    }
  },
  data() {
    return {
      rotateArrow: false,
      inputA: "",
      inputB: "",
      tokenName: "EX",
      nombreFrom: "Ethereum",
      logoFrom: require("@/assets/logo/ethereum.png"),
      filterFrom: false,
      nombreUp: "EXS",
      descUp: "Exswap",
      logoUp: require("@/assets/logo/logo.png"),
      filterUp: false,
      nombreTo: "Evmos",
      logoTo: require("@/assets/logo/logo.png"),
      filterTo: false,
    }
  },
  methods: {
    limitNumber (id) {
      var input = document.getElementById(id);
      input.addEventListener('input', function() {
        if (this.value.length > 13) {
          this.value = this.value.slice(0, 13)
        }
      })
    },
    SwitchButton () {
      this.rotateArrow = !this.rotateArrow;
      let logo = this.logoFrom;
      let nombre = this.nombreFrom;

      this.logoFrom = this.logoTo
      this.nombreFrom = this.nombreTo
      if (this.nombreFrom == "Evmos") {
        this.filterFrom = true
      } else {
        this.filterFrom = false
      }

      this.logoTo = logo
      this.nombreTo = nombre
      if (this.nombreTo == "Evmos") {
        this.filterTo = true
      } else {
        this.filterTo = false
      }
    },
    SelectTokenA (item) {
      this.tokenName = item.name;
      this.nombreUp = item.name;
      this.logoUp = item.logo;
      if (this.nombreUp == "EXS") {
        this.filterUp = true
      } else {
        this.filterUp = false
      }
      this.$refs.menu.menuA = false;
    },
    SelectTokenFrom (item) {
      if (item.name == this.nombreTo) {
        let logo = this.logoFrom;
        let nombre = this.nombreFrom;

        this.logoFrom = item.logo
        this.nombreFrom = item.name
        if (this.nombreFrom == "Evmos") {
          this.filterFrom = true
        } else {
          this.filterFrom = false
        }

        this.logoTo = logo
        this.nombreTo = nombre
        if (this.nombreTo == "Evmos") {
          this.filterTo = true
        } else {
          this.filterTo = false
        }
        this.$refs.menu.menuFrom = false;
      } else {
        this.nombreFrom = item.name;
        this.logoFrom = item.logo;
        if (this.nombreFrom == "Evmos") {
          this.filterFrom = true
        } else {
          this.filterFrom = false
        }
        this.$refs.menu.menuFrom = false;
      }
    },
    SelectTokenTo (item) {
      if (item.name == this.nombreFrom) {
        let logo = this.logoTo;
        let nombre = this.nombreTo;

        this.logoTo = item.logo
        this.nombreTo = item.name
        if (this.nombreTo == "Evmos") {
          this.filterTo = true
        } else {
          this.filterTo = false
        }

        this.logoFrom = logo
        this.nombreFrom = nombre
        if (this.nombreFrom == "Evmos") {
          this.filterFrom = true
        } else {
          this.filterFrom = false
        }
        this.$refs.menu.menuTo = false;
      } else {
        this.nombreTo = item.name;
        this.logoTo = item.logo;
        if (this.nombreTo == "Evmos") {
          this.filterTo = true
        } else {
          this.filterTo = false
        }
        this.$refs.menu.menuTo = false;
      }
    },
    AlertaSuccess() {
      this.$refs.alerta.AlertSuccess();
    },
  }
};
</script>

<style src="./Bridge.scss" lang="scss"></style>
<style scoped>
.openMenuFromB {
  cursor: pointer;
}
.openMenuABridge {
  cursor: pointer;
}
