  methods: {
    limitNumber (id) {
      var input = document.getElementById(id);
      input.addEventListener('input', function() {
        if (this.value.length > 12) {
          this.value = this.value.slice(0, 12)
        }
      })
    },
    botonD(item) {
      if (item.activeDeposit == false) {
        item.activeDeposit = true
        item.activeWithdraw = false
      }
    },
    botonW(item) {
      if (item.activeWithdraw == false) {
        item.activeDeposit = false
        item.activeWithdraw = true
      }
    },
    botonH(item) {
      if (item.activeHalf == false) {
        item.activeHalf = true
        item.activeMax = false
      }
    },
    botonM(item) {
      if (item.activeMax == false) {
        item.activeMax = true
        item.activeHalf = false
      }
    },
    controls(item, key) {
      if (key == 'more') {
        item.controls = true;
      }
      if (key == 'less') {
        item.controls = false;
      }
